.vds-date-picker {
  &-header {
    align-items: center;
    border-bottom: 1px solid $color-date-picker-calendar-header-divider;
    display: flex;
    height: 44px;
  }

  &-day {
    color: $color-indigo;
    font-weight: 500;
    margin: 0 auto;
  }

  &-day-name {
    color: $color-indigo;
    height: 20px;
    padding: 8px 0;
  }

  &-header-title {
    color: $color-indigo;
    font-weight: 700;
    flex-grow: 1;
    text-align: center;
  }

  &-footer {
    text-align: right;
  }

  &-today {
    border: 1px solid $color-date-picker-calendar-date-btn-label-border--current;
  }

  &-next-prev-month {
    color: theme(date-picker-calendar-dates-btn-previous-and-next-month);
  }

  &-calendar {
    background-color: $color-date-picker-calendar;
    box-shadow: $color-date-picker-calendar-shadow;
    padding: 8px;
    z-index: 1;

    &-grid {
      width: 100%;
    }

    td {
      height: 44px;
      width: 44px;
    }
  }

  &-button-cancel {
    margin: 0;
  }

  &-in-range,
  &-mouse-enter-range {
    background-color: theme(button-icon-light-background--hover);
  }

  .vds-state {
    &-selected {
      color: $color-date-picker-calendar-dates-btn-text--selected;
      background-color: $color-date-picker-calendar-dates-btn-background--selected;

      &.vds-state-focused {
        color: $color-date-picker-calendar-dates-btn-text--selected;
        background-color: $color-date-picker-calendar-dates-btn-background--selected;
      }
    }

    &-disabled {
      color: $color-date-picker-input-btn-icon--readonly-disabled;
    }

    &-focused {
      background-color: theme(button-icon-light-background--hover);
    }

    &-highlighted {
      color: $color-date-picker-calendar-dates-btn-text--selected;
      background-color: $color-date-picker-calendar-header-divider;
    }
  }

  .vds-btn-label {
    pointer-events: none;
  }
}

